/* List styles */
/* list component */
ig-list {
  overflow: hidden; }
  ig-list .ig-list-inner {
    /*background: lightgreen;*/
    background-color: #f4f4f4;
    overflow: hidden; }
  ig-list a {
    -moz-border-radius: 5px;
    -webkit-border-radius: 5px;
    border-radius: 5px;
    display: inline-block;
    padding: 10px 25px 10px 10px;
    position: relative;
    text-decoration: none; }
  ig-list a:before, ig-list a:after {
    border-right: 2px solid;
    content: '';
    display: block;
    height: 8px;
    margin-top: -6px;
    position: absolute;
    -moz-transform: rotate(135deg);
    -o-transform: rotate(135deg);
    -webkit-transform: rotate(135deg);
    transform: rotate(135deg);
    right: 10px;
    top: 50%;
    width: 0; }
  ig-list a:after {
    margin-top: -1px;
    -moz-transform: rotate(45deg);
    -o-transform: rotate(45deg);
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg); }
  ig-list a:hover, ig-list a:focus,
  ig-list :hover:before, ig-list a:hover:after,
  ig-list a:focus:before, ig-list a:focus:after {
    color: green; }

/* header component */
ig-header {
  overflow: hidden; }
  ig-header .ig-header-inner {
    /*color: #000;
        background: #f4f4f4;
        padding: 3px 5px 2px 3px;*/
    color: #444;
    background-color: #efeff4;
    padding: 8px;
    border-bottom: 1px solid #c8c7cc;
    letter-spacing: 1px;
    text-transform: uppercase; }

/* item component */
ig-item {
  overflow: hidden;
  display: list-item;
  list-style-type: none; }
  ig-item .ig-item-inner {
    /*padding-left: 7px;*/
    background: #fff;
    color: #444;
    position: relative;
    padding: 8px 16px;
    border-bottom: 1px solid #c8c7cc;
    word-wrap: break-word; }

